@import url('https://fonts.googleapis.com/css?family=Open+Sans');

html,body {
  font-family: "Open Sans", serif;
  background: rgba(255,255,255,1);
  background: -moz-linear-gradient(-45deg, rgba(255,255,255,1) 0%, rgba(250,250,208,1) 18%, rgba(245,206,206,1) 38%, rgba(73,165,191,1) 100%);
  background: -webkit-gradient(left top, right bottom, color-stop(0%, rgba(255,255,255,1)), color-stop(18%, rgba(250,250,208,1)), color-stop(38%, rgba(245,206,206,1)), color-stop(100%, rgba(73,165,191,1)));
  background: -webkit-linear-gradient(-45deg, rgba(255,255,255,1) 0%, rgba(250,250,208,1) 18%, rgba(245,206,206,1) 38%, rgba(73,165,191,1) 100%);
  background: -o-linear-gradient(-45deg, rgba(255,255,255,1) 0%, rgba(250,250,208,1) 18%, rgba(245,206,206,1) 38%, rgba(73,165,191,1) 100%);
  background: -ms-linear-gradient(-45deg, rgba(255,255,255,1) 0%, rgba(250,250,208,1) 18%, rgba(245,206,206,1) 38%, rgba(73,165,191,1) 100%);
  background: linear-gradient(135deg, rgba(255,255,255,1) 0%, rgba(250,250,208,1) 18%, rgba(245,206,206,1) 38%, rgba(73,165,191,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#49a5bf', GradientType=1 );
}

.wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
 
}

.main {
  height: 66vh;
  background: #2a6bb1;
  box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
  width: 60%;
  margin: 0 auto;
}

.info {
  height: 66vh;
  background: url("img/weather.jpg") center center no-repeat;
  background-blend-mode: multiply;
  background-color: #8b8b8b;
  border: 2px solid #fff;
  border-right: 2px solid #fff;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

.form {
  height: 66vh;
  padding: 5px;
  border-top: 2px solid #fff;
  border-bottom: 2px solid #fff;
  border-right: 2px solid #fff;
  background-color: #103a6554;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 2px #113a65;
  width: 50%;
  padding-bottom: 4px;
  color: #fff;
  font-weight: lighter;
  font-size: 20px;
  outline: none;
  width: 100%;
  margin: 20px 0;
}

input::-webkit-input-placeholder {
  color: #e0e0e0;
}

input::-moz-placeholder {
  color: #e0e0e0;
}

button {
  outline: 2px solid #103a65;
  border-bottom: 2px solid #103a65;
  padding: 8px 20px;
  margin: 10px 0 ;
  border-radius: 2px;
  cursor: pointer;
  background-color: #f1ea86;
  color: rgb(39, 40, 94);
  outline: none!important;
  width: 100%;
  text-transform: uppercase;
}

button:hover {
  outline: 2px solid #103a65;
  position: relative;
  top: 2px;
}

button:active {
 outline: 2px solid transparent;
  border-color: transparent;
  position: relative;
  top: 2px;
}

.info h2 {
  font-size: 3em;
  color: #fff;
}

.info p {
  color: #e4e4e4;
 
}

.infoWeath {
  padding: 0;
  font-size: 20px;
  color: #e0e0e0;
  background-color: #103a659f;
  padding: 5px;
}

.error {
  color: #fdee69;
}